import DateInfo from 'components/TableInfoComponents/DateInfo/DateInfo';
import CellGrid from '../../TableInfoComponents/Cells/CellGrid';
import Status from 'components/TableInfoComponents/Status/Status';
// import { useSelector } from 'react-redux';
// import { getPageObjData } from 'redux/selectors';

import s from './OrderTopInfo.module.scss';

const OrderPaymentInfo = ({ paymentInfo }) => {
  // const order = useSelector(getPageObjData);

  const mapData = [
    { name: ' ', inner: <Status status={paymentInfo?.type} /> },
    { name: '', inner: <Status status={paymentInfo?.status} /> },
    { name: '', inner: <DateInfo dateString={paymentInfo?.updatedAt} /> },
    { name: ' ', inner: paymentInfo?.blockedFunds || 0 },
    { name: ' ', inner: paymentInfo?.receivedFunds || 0 },
    { name: ' ', inner: paymentInfo?.receivedBonuses || 0 },
    { name: '   (ID)', inner: paymentInfo?.paymentDocumentID || '<<<--->>>' },
    { name: '    (ID)', inner: paymentInfo?.invoiceId || '<<<--->>>' },
    { name: '    (ID)', inner: paymentInfo?.holdingInvoiceId || '<<<--->>>' },
  ];
  return (
    <>
      <div className={s.topInfo}>
        {mapData.map((el, idx) => (
          <CellGrid key={idx} name={el.name} text={el.inner} title={el?.title} />
        ))}
      </div>
    </>
  );
};
export default OrderPaymentInfo;
