import React from 'react';
import DateInfo from 'components/TableInfoComponents/DateInfo/DateInfo';
import Status from 'components/TableInfoComponents/Status/Status';
import RowSimple from 'components/TableInfoComponents//RowSimple/RowSimple';
import { useBlock } from 'components/Block/BlockContext';
import { useSelector } from 'react-redux';
import { getPageObjData } from 'redux/selectors';

import s from './TableProductInfo.module.scss';

const TableProductInfo = () => {
  const post = useSelector(getPageObjData);
  const { printRef } = useBlock();

  const { createdBy, updatedBy, categoryInfo, availabilityInfo, priceInfo } = post;

  return (
    <>
      <div className={s.table} ref={printRef}>
        <RowSimple title="" data={<Status status={post?.approvedStatus} />} info="  " />
        <RowSimple
          title=""
          data={<Status status={post?.visibilityStatus ? 'visible' : 'hidden'} />}
          info="    "
        />

        <RowSimple title="SKU" data={post?.sku} />
        <RowSimple title="" data={post?.name} />
        <RowSimple title="" data={post?.brand} />

        <RowSimple title=",  ()" data={<DateInfo dateString={post?.createdAt} />} />
        <RowSimple
          title=" (ID, )"
          data={`${createdBy?.authorName || 'author'} (${createdBy?.authorId || '0000'}, ${createdBy?.authorType || 'vendor'})`}
        />

        <RowSimple title=",  ()" data={<DateInfo dateString={post?.updatedAt} />} />
        <RowSimple
          title=" (ID, )"
          data={`${updatedBy?.updatorName || 'author'} (${updatedBy?.updatorId || '0000'}, ${updatedBy?.updatorType || 'vendor'})`}
        />

        <RowSimple title="" data={`${priceInfo?.price} `} />
        <RowSimple title="" data={`${priceInfo?.cost} `} />
        <RowSimple title=", ID" data={`${priceInfo?.cashbackId} `} />
        <RowSimple
          title=""
          data={<Status status={priceInfo?.isCommission ? 'isCommission' : 'isStandart'} />}
          info="  "
        />
        <RowSimple title=", %" data={`${priceInfo?.commission} `} />
        <RowSimple title=", %" data={`${priceInfo?.sale} `} />
        <RowSimple title="" data={`${priceInfo?.currency} `} />

        <RowSimple title=" (ID)" data={`${categoryInfo?.section} (${categoryInfo?.sectionId})`} />
        <RowSimple title="  (ID)" data={`${categoryInfo?.parentCategory} (${categoryInfo?.parentCategoryId})`} />
        <RowSimple title=" (ID)" data={`${categoryInfo?.category} (${categoryInfo?.categoryId})`} />

        <RowSimple title="" data={`${post?.description || ''}`} />
        <RowSimple title=" " data={`${post?.innerComment}` || ''} info="       " />

        <RowSimple title="" data={<Status status={availabilityInfo?.availability} />} />
        <RowSimple title=" ( )" data={`${availabilityInfo?.order} (${availabilityInfo?.orderAwaitingTime})`} />
        <RowSimple
          title="  ( )"
          data={`${availabilityInfo?.specialOrder} (${availabilityInfo?.specialOrderAwaitingTime})`}
        />

        <RowSimple title="  ..." />

        <RowSimple title=" ..." />
      </div>
    </>
  );
};

export default TableProductInfo;
